// user.js
const mongoose = require('mongoose');
const { Schema } = mongoose;

const userSchema = new Schema(
  {
    // ... your other fields
    email: {
      type: String,
      trim: true,
      required: true,
      unique: true,
    },
    password: {
      type: String,
      required: true,
      min: 6,
      max: 64,
    },
    // ... your other fields
  },
  { timestamps: true }
);

const User = mongoose.model('User', userSchema);

module.exports = User;
